import { Grid, Paper, TableContainer } from '@mui/material';
import { graphql, PageProps } from 'gatsby';
import { MDXRenderer } from 'gatsby-plugin-mdx';
import { MDXProvider } from '@mdx-js/react';
import React from 'react';
import ToC, { ToCItem } from '../layouts/toc';
import Document from '../layouts/document';

interface Data {
  mdx: {
    id: string;
    body: string & React.ReactNode;
    tableOfContents: {
      items: ToCItem[];
    };
  };
}

export const query = graphql`
  query MdxQuery($id: String!) {
    mdx(id: { eq: $id }) {
      id
      body
      tableOfContents
    }
  }
`;

const Article: React.FC<PageProps<Data>> = (props) => {
  const {
    data: { mdx },
  } = props;
  return (
    <Document>
      <Grid container spacing={2}>
        <Grid item xs={9}>
          <Paper
            className="blog-page"
            elevation={0}
            sx={(theme) => ({
              padding: theme.spacing(2, 4),
              mt: theme.spacing(2),
              mb: theme.spacing(2),
            })}
          >
            <MDXProvider
              components={{
                table: (props) => (
                  <TableContainer>
                    <table {...props} />
                  </TableContainer>
                ),
              }}
            >
              <MDXRenderer>{mdx.body}</MDXRenderer>
            </MDXProvider>
          </Paper>
        </Grid>
        <Grid item xs={3}>
          {Boolean(mdx.tableOfContents?.items?.length) && (
            <ToC items={mdx.tableOfContents?.items || []} />
          )}
        </Grid>
      </Grid>
    </Document>
  );
};

export default Article;
