import {
  Box,
  List,
  ListItem,
  ListItemText,
  ListSubheader,
  Paper,
  Typography,
} from '@mui/material';
import { Link } from 'gatsby';
import React from 'react';

export interface ToCItem {
  url: string;
  title: string;
  items?: ToCItem[];
}

export interface ToCProps {
  items: ToCItem[];
}

const ToC: React.FC<ToCProps> = (props) => {
  const { items } = props;

  return (
    <List
      sx={(theme) => ({
        width: '100%',
        bgcolor: 'background.paper',
        position: 'sticky',
        overflow: 'auto',
        mt: theme.spacing(2),
        top: '80px',
      })}
    >
      <ListSubheader>目录</ListSubheader>
      {items?.map((item, index) => {
        return (
          <ListItem key={`item-${index}`} component={Link} to={item.url}>
            <ListItemText>{item.title}</ListItemText>
          </ListItem>
        );
      })}
    </List>
  );

  return (
    <Box>
      <Paper
        elevation={0}
        sx={(theme) => {
          return {
            padding: theme.spacing(2),
            mt: theme.spacing(2),
            mb: theme.spacing(2),
            overflow: 'hidden',
            position: 'sticky',
            top: theme.spacing(4),
          };
        }}
      >
        <Typography>目录</Typography>
        {items?.map((item, index) => {
          return (
            <Box key={index}>
              <Link to={item.url}>{item.title}</Link>
            </Box>
          );
        })}
      </Paper>
    </Box>
  );
};

export default ToC;
